@import url('https://fonts.googleapis.com/css?family=Ubuntu');
body {
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', sans-serif;
}
.logo{
	width: 32px;
}
.logo-div{
	text-align: center;
	width: 32px;
    margin-right: 20px;
}
.gray{
	color: #777;
}
.search-bar{
	padding: 10px 20px;
}
.highlight{
	color: red;
}
.media-body{
	    padding: 0 10px;
}
.list-group-item{
	cursor: pointer;
	-webkit-transition: 0.3s;
	-o-transition: 0.3s;
	transition: 0.3s;
	border: 1px solid #eee;
}
.media-heading{
	-webkit-transition: 0.2s;
	-o-transition: 0.2s;
	transition: 0.2s;
}
.list-group-item:hover{
	background: #f9f9f9;
	-webkit-box-shadow: inset 0 0 15px #eee;
	        box-shadow: inset 0 0 15px #eee;
	-webkit-transform: scale(0.997);
	    -ms-transform: scale(0.997);
	        transform: scale(0.997);
}
.embed-responsive-16by9{
	border-radius: 5px;
	margin-bottom: 15px;
}
.fa-check-circle{
	margin-left: 5px;
	color: #999;
}


